.header{
    height: 60px;
    display: flex;
    align-items: center;
    background-color: #131921;
    position: sticky;
    top: 0;
    z-index: 100;
}
.header_logo{
    width: 100px;
    object-fit: contain;
    margin: 0 20px;
    margin-top: 18px;
}
.header_search{
    display: flex;
    flex: 1;
    align-items: center;
    border-radius: 24px;
}
.header_searchInput{
    height: 12px;
    padding: 10px;
    border:none;
    width: 100%;
}
.header_searchIcon{
    padding: 5px;
    height: 22px !important;
    background-color: #cd9042;
}

.header_nav{
    display: flex;
    justify-content: space-evenly;
    color: white;
}
.header_option{
    display: flex;
    flex-direction: column;
    margin: 0 10px;
    list-style-type: none;
    text-decoration: none;
    color: #fff;
}
.header_optionOne{
    font-size: 10px;
}
.header_optionTwo{
    font-size: 13px;
    font-weight: 800;
}
.header_optionBasket{
    display: flex;
    align-items: center;
    color: white;   
}
.header_basketCount{
    margin: 0 10px;

}