.product{
    display: flex;
    flex-direction: column;
    align-items:  center;
    justify-content: center;
    margin: 10px;
    padding: 20px;
    max-height: 400px;
    min-width: 100px;
    background-color: #fff;
    z-index: 1;
    width: 100%;
}
.product_info{
    margin-bottom: 15px;
    height: 100px;
}
.product_price{
  margin-top: 5px;
}
.product_rating{
    display: flex;
}
.product > img{
    width: 100%;
    max-height: 200px;
    object-fit: contain;
    margin-bottom: 15px;
}
.product_button{
    background: #f0c14b;
    border: 1px solid;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #846a29;
    color: #111;
    cursor: pointer;
}