.home_image{
    width: 100%;
    margin-bottom: -150px;
    z-index: -1;
    mask-image: linear-gradient(to bottom , rgba(0,0,0,1) , rgba(0,0,0,0));
}
.home{
    display: flex;
    justify-content: center;
    flex-direction: column;
    margin-left: auto;
    margin-right: auto;
    max-width: 1500px;
}
.home_row{
    z-index: 1;
    display: flex;
    margin:0 5px;
    justify-content: center;
}